$img-path: "/images";
$light-black: rgba(0,0,0,0.5);
$blue-color: #00A2E0;
$link-color: #00A2E0;
$color_gravel_approx: #484848;
$color_dodger_blue_approx: #00A8FF;
$color-cerulean-approx: #0598e5;
$primary-color: #1AAB3C;
$color-link-hover: #0F942E;
$url_3: url(../../../images/ic-sprite.svg);
$url_5: url(../../../images/ic-no-activity.svg);
$url_6: url(../../../images/ic-no-document.svg);

body{

    &.front-page{
        background: #fff;
    }
}

body.country-specific-wrap{
    background: #fff;
}

a{
    text-decoration: none;
    color: $primary-color;

    &:hover,&:focus,&:active{
        color: $color-link-hover;
        text-decoration: underline;
    }
}

::-webkit-input-placeholder {
    color: rgba(72,72,72,0.72);
}

::-moz-placeholder {
    color: rgba(72,72,72,0.72);
}

:-ms-input-placeholder {
    color: rgba(72,72,72,0.72);
}

body:not(.front-page,.lite) {
    .navbar {
        border-bottom: solid 1px rgba(151,151,151,0.2);
    }

    @media (min-width: 768px) {
        .navbar {
            padding: 18px 30px;
        }
    }
}

body.country-specific-wrap{
    background: #fff;
}

h1{
    line-height: 55px;

    @media (max-width: 480px) {
        font-size: 24px;
        line-height: 30px;
    }
}

h2{
    font-size: 24px;
    line-height: 33px;
    margin-bottom: 20px;

    @media (max-width: 480px) {
        font-size: 18px;
        line-height: 24px;
    }
}

.introduction-wrapper {
    padding: 90px 0 50px;
    h1 {
        a {
            color: #484848;
            border-bottom: 2px solid $primary-color;
            box-shadow: 0px 2px 0px #FCDC38;
        }
    }
}

@media (max-width: 480px) {
    .introduction-wrapper{
        padding-top: 40px;
        h1{
            font-size: 24px;
            line-height: 36px;
        }
    }
}


@media (min-width: 768px){
    .introduction-wrapper h2 {
        width: 694px;
        margin: 0 auto 30px;
    }
}

.title{
    font-family: Open-Sans-Bold;
    margin-bottom: 15px;
}

.title-section {
    margin-bottom: 20px;
    padding: 40px 20px 20px;

    h1{
        margin-bottom: 8px;
    }

    & > div, h1{
        text-align: center;
    }

    & > div:first-of-type{
       color: rgba(0,0,0,0.7);
        margin-bottom: 22px;
    }

    & > div:nth-child(3){
        font-size: 11px;
        line-height: 17px;
        color: $light-black;
    }

    @media (max-width: 480px) {
        padding: 20px 20px;
        margin-bottom: 0;
    }
}

.container--shadow{
    background-color: #fff;
    box-shadow: 2px 2px 10px 0 rgba(0,0,0,0.28);
    border-radius: 4px;
    margin-top: 10px;
}


/* style for empty states */

.no-data {
    font-size: 16px;
    font-style: italic;
    color: rgba($color_gravel_approx, 0.7);
    padding: 44px 0 50px;
    background: rgba($color_gravel_approx, 0.06);
    margin: 36px 30px;
    .btn {
        background: $color_dodger_blue_approx $url_3 no-repeat -479px -138px;
        border: 0;
        line-height: 67px;
        padding: 0 34px 0 62px;
        font-size: 16px;
        display: table;
        margin: 25px auto;
        font-style: normal;
        &:hover {
            background-color: $color_cerulean_approx;
        }
    }
}

.no-activity-data {
    background: rgba($color_gravel_approx, 0.06) $url_5 no-repeat center 79px;
    padding: 204px 0 20px;
}

.no-document-data, .no-result-data {
    background: rgba($color_gravel_approx, 0.06) $url_6 no-repeat center 79px;
    padding: 204px 0 20px;
}

.btn,input[type="submit"],.btn-register {
    background-color: $primary-color!important;
    border: 0!important;
    &:hover,&:active,&:focus {
        background-color: $color-link-hover!important;
        border: 0!important;
    }
}

.btn-link,.modal-dialog .close span, .modal-dialog .close:hover span, .modal-dialog .close:focus span {
    color: $primary-color;
    &:hover {
        color: $color-link-hover;
    }
}

.navbar-toggle.collapsed span {
    background: $primary-color;
}

input:-webkit-autofill, input:-webkit-autofill:focus, select:-webkit-autofill, select:-webkit-autofill:focus, textarea:-webkit-autofill, textarea:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0 1000px #f4fdf7 inset!important;
    box-shadow: 0 0 0 1000px #f4fdf7 inset!important;
}

